export function fetchListsSucceeded(lists) {
	return {
		'type': 'FETCH_LISTS_SUCCEEDED',
		'payload': {
			lists
		}
	};
}


export function createList({ title, description }) {
	return {
		'type': 'CREATE_LIST',
		'payload': {
			'id': uniqueId(),
			title,
			description
		}
	};
}

export function setListIsPublic({ id, is_public }) {
	return {
		'type': 'SET_LIST_IS_PUBLIC',
		'payload': {
			'id': id,
			is_public
		}
	};
}
